<template>
  <div class="upload-index">
    <div :class="[uploadFoces ? 'upload-fo' : '' , 'upload-box']" v-paste="handlePaste" tabindex="-1" @focus="upFoces" @blur="upBlur">
      <p>ctrl+V</p>
      <!-- :before-remove="beforeRemove" -->
      <el-upload
        class="upload-demo"
        ref="upload"
        action="container"
        :before-upload="beforeAvatarUpload"
        :http-request="uploadFiles"
        :on-remove="handleRemove"
        :limit="num"
        :on-change="handleChange"
        :on-exceed="handleExceed"
        :file-list="imgList"       
        drag
        multiple>
        <el-button size="mini" type="primary">{{title}}</el-button>
        <div slot="tip" class="annotation">PS: {{ maxSize }}M</div>
      </el-upload>
    </div>

    <div style="display: none;">
      <img ref="qfUploadLogo" class="qf-upload-logo" :src="watermarkImg || '../../assets/qf_logo.png'">
    </div>
  </div>
</template>

<script>
import paste from '../../directive/el-paste' // paste directive

export default {
  name: 'QfQiniuUpload',
  directives: { paste },
  props: {
    orderId: {   // ID 
      type: String,
      default: '',
    },
    title: {
      type: String,
      default: '',
    },
    dir: {
      type: String,
      default: 'dev'
    },
    num: {
      type: Number,
      default: 9
    },
    uploadType: {
      /* 1  10  */
      type: String,
      default: '1'
    },
    uploadName: {
      /* 20  21  22  23  24  */
      type: String,
      default: '24'
    },
    uploadStatus: {
      default: ''
    },
    upNum: {
      type: Number,
      default: 0
    },
    watermark: {  // 
      type: Boolean,
      default: true
    },
    watermarkImg: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      upToken: '',
      maxSize: 500,
      imgList: [],
      fileList: [],
      dialogImageUrl: '',
      dialogVisible: false,
      uploadFoces: false,
      showFileList: true,
    }
  },
  created () {
  },
  mounted() {
  },
  methods: {
    upNumAddSub(n){
      let num = this.upNum + n
      this.$emit('update:upNum', num)
    },
    handleRemove(file, fileList) {
      // this.fileList = fileList
      for(var i in this.imgList){
        if(file.id == this.imgList[i].id){
          this.imgList.splice(i,1)
        }
      }
      this.$refs.upload.abort(); //
    },
    beforeRemove(file, fileList) {
      if(file && file.status === 'success'){
        return this.$confirm(``)
      }
    },
    handleExceed(files, fileList) {
      this.$message.warning(` ${this.num}  ${files.length}  ${files.length + fileList.length} `)
    },
    beforeAvatarUpload(file) {
      let _this = this
      return new Promise((resolve, reject) => {
        let size = file.size / 1024 / 1024
        let isLtM = size < this.maxSize //  this.maxSize
        let isJPG = (file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png');
        if(!isLtM) {
          reject()
        }
        let isSize = 1 // 
        if(size > 2 && size <= 5){
          isSize = 0.9
        }else if(size > 5 && size <= 10){
          isSize = 0.8
        }else if(size > 10){
          isSize = 0.7
        }
        if(isJPG && isSize){
          let image = new Image(), resultBlob = '';
          image.src = URL.createObjectURL(file);
          image.onload = () => {
            // blob
            resultBlob = _this.compressUpload(image, file, isSize);
            resolve(resultBlob)
          }
          image.onerror = () => {
            reject()
          }
        }else{
          resolve(file)
        }
      })
    },
    handleChange(file, fileList) {
      for(var i in this.imgList){
        if(this.imgList[i].uid == file.uid){
          return
        }
      }
      this.imgList.push(file)
    },
    uploadFiles(params, type) {
      let that = this
      this.upNumAddSub(1)
      that.showFileList = true
      let file = '', key = '', name = '';
      let dirName = process.env.VUE_APP_TITLE === 'stage' ? `${this.dir}_dev` : this.dir
      if(type && type == 'paste'){
        file = params; // 
      }else{
        file = params.file; // 
      }
      name = file.name;
      if(this.uploadStatus){
        var name2 = name.split('.')
        if(this.uploadStatus == 1){
          name = '' + this.dateFormat(new Date(), 'date') + '.' + name2[name2.length - 1]
          file = new File([file], name)
        }else if(this.uploadStatus == 2) {
          name = '' + '.' + name2[name2.length - 1]
          file = new File([file], name)
        }
      }
      key = `${dirName}/${new Date().getTime()}_${name}`; // 
      /*
      * config.useCdnDomain:  cdn true or false false
      * config.disableStatisticsReport: false
      * config.region:  null  undefined 
      * config.retryCount: 3
      * config.concurrentRequestLimit: number3
      * config.checkByMD5:  MD5  false
      */
      let config = {
        useCdnDomain: true, 
        concurrentRequestLimit: 1, // 1
        // region: qiniu.region.z0
      };

      /*
      * fname: string.
      * params: object;
      * mimeType: null || array null 
      *  ["image/png", "image/jpeg", "image/gif"]
      */
      let paramsObj = null
      if(this.uploadType == 10){
        paramsObj = {
          'x:type': '' + this.uploadType,
          'x:typeId': 1,
        }
      }else {
        paramsObj = {
          'x:type': '' + this.uploadType,
          'x:typeId': '' + this.orderId,
          'x:field': '' + this.uploadName,
          'x:val': '' + this.uploadStatus,
        }
      }
      let putExtra = {
        fname: name,
        params: paramsObj, 
        // mimeType: ["image/png", "image/jpeg", "image/gif"]
      };
      let observe = {
        next (res) {
          // console.log('' + res.total.loaded)
          // console.log('' + res.total.size)
          // console.log('0100' + res.total.percent);
          if(params.onProgress){
            params.onProgress({percent: res.total.percent})
          }
        },
        error (err) {
          that.upNumAddSub(-1)
          that.$notify.closeAll()
          that.$notify.error(`"${name}"`)
          that.imgList.forEach((element, i) => {
            if(element.uid == file.uid){
              that.imgList.splice(i, 1)
            }
          });
          // console.log('', err)
        },
        complete (res) {
          that.upNumAddSub(-1)
          //
          //key  hash  key
          // that.showFileList = false
          that.$notify.closeAll()
          that.$notify.success(`"${name}"`)
          that.imgList.forEach((element, i) => {
            if(element.uid == file.uid){
              that.imgList.splice(i, 1)
            }
          });
          
          that.$emit('getBig', res.uploads, that.uploadStatus)
        }
      };
      //  token ()
      this.$notify({
        message: `"${name}"...`,
        duration: 0
      });
      this.$emit('qiniuFn', (upToken) => {
        let observable = qiniu.upload(file, key, upToken, putExtra, config)
        let subscription = observable.subscribe(observe)
        // subscription.unsubscribe(); // 
      })
    },

    /* -canvas */
    compressUpload(image, file, multiple) {
      var logoImg = this.$refs.qfUploadLogo
      let canvas = document.createElement('canvas')
      let ctx = canvas.getContext('2d')
      let initSize = image.src.length
      let { width, height } = image
      canvas.width = width
      canvas.height = height
      ctx.fillRect(0, 0, canvas.width, canvas.height)
      ctx.drawImage(image, 0, 0, width, height)
      
      //  logoImg
      if(this.watermark){
        ctx.rotate(-20 * Math.PI / 180)
        ctx.translate(-width/2, -height/2);
        ctx.font = '18px Vedana'
        ctx.fillStyle = '#000'
        ctx.textAlign = 'center'
        ctx.textBaseline = 'Middle'
        ctx.globalAlpha = 0.15
        //
        for (let i = 0; i < width / 110; i++) {  
          for (let j = 0; j < height / 35; j++) {
            if(i % 2 === j % 2){
              let w = i * 300;
              let h = j * 80
              ctx.drawImage(logoImg, w - 180, h - 30, 93, 36)
              ctx.fillText('', w, h);
            }
          }
        }
      }
      
      // 0.1
      let compressData = canvas.toDataURL('image/jpeg', multiple)
      // base64Blob
      let blobImg = this.dataURLtoFile(compressData, file.name, file.type)
     
      return blobImg
    },
    
    /* base64Blob */
    dataURItoBlob(data) {
      let byteString;
      if(data.split(',')[0].indexOf('base64') >= 0) {
          byteString = atob(data.split(',')[1])
      }else {
          byteString = unescape(data.split(',')[1])
      }
      let mimeString = data
          .split(',')[0]
          .split(':')[1]
          .split(';')[0];
      let ia = new Uint8Array(byteString.length)
      for( let i = 0; i < byteString.length; i += 1) {
          ia[i] = byteString.charCodeAt(i)
      }
      return new Blob([ia], {type: mimeString})
    },
    /** 
     * @method base64file
     * @param {String} dataURL base64(base64data:image/jpeg;base64,/XXXXXXXXXX)
     * @param {String} fileName ()
     * @param {String} fileType  image/jpg
     * @return {Object} file
    */
    dataURLtoFile(dataURL, fileName, fileType) {
      var arr = dataURL.split(','), mime = arr[0].match(/:(.*?);/)[1],
      bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
      while(n--){
        u8arr[n] = bstr.charCodeAt(n);
      }
      return new File([u8arr], fileName, {type:fileType || 'image/jpg'});  //file
    },

    // 
    handlePaste(e) { 
      let _this = this
      let file = null
      if (e.clipboardData && e.clipboardData.items[0] && e.clipboardData.items[0].type && e.clipboardData.items[0].type.indexOf('image') > -1) { //
        file = e.clipboardData.items[0].getAsFile();
      }else {
        this.$message({
          type: 'warning',
          message: ''
        })
        return;
      }

      let image = new Image(), resultBlob = '';
      image.src = URL.createObjectURL(file);
      image.onload = () => {
        // blob
        resultBlob = _this.compressUpload(image, file, 1);
        _this.uploadFiles(resultBlob, 'paste')
      }
      image.onerror = () => {
        this.$message.error('')
      }
    },
    // 
    upFoces() {
      this.uploadFoces = true
    },
    upBlur() {
      this.uploadFoces = false
    },

    //
    dateFormat(data, type) {
      if(data){
        data = typeof data === 'string' ? data.replace(/-/g, '/') : data
        var now = new Date(data);
        var year = now.getFullYear();       //
        var month = now.getMonth() + 1;     //
        var day = now.getDate();            //

        var hh = now.getHours();            //
        var mm = now.getMinutes();          //
        var ss = now.getSeconds();          //

        if (month < 10) {
          month = "0" + month;
        }
        if (day < 10) {
          day = "0" + day;
        }
        if (hh < 10) {
          hh = "0" + hh;
        }
        if (mm < 10) {
          mm = "0" + mm;
        }
        if (ss < 10) {
          ss = "0" + ss;
        }
        if (type == 'date') {
          return (year + "-" + month + "-" + day);
        } else if (type == 'month') {
          return (year + "-" + month);
        } else if (type == 'time') {
          return (hh + ':' + mm + ':' + ss)
        } else if (type == 'md') {
          return (month + '.' + day)
        } else {
          return (year + "-" + month + "-" + day + ' ' + hh + ':' + mm + ':' + ss);
        }
      }else{
        return ''
      }
    }
  }
}
</script>



